import { ThemedStyledProps } from 'styled-components';

export type Colors = 'primary' | 'red' | 'dark-red' | 'white' | 'black';

export type FontFamilies = 'primary' | 'secondary';

export type PaddingSize = 'mobile' | 'desktop';

export interface Theme {
  color: { [key in Colors]: string };
  fontFamily: { [key in FontFamilies]: string };
  paddingSize: string;
  defaultRadius: string;
}

export type ThemeProps<T = {}> = ThemedStyledProps<T, Theme>;

export const theme: Theme = {
  color: {
    primary: '#044aaf',
    red: '#ff3e5b',
    white: '#ffffff',
    'dark-red': '#ee2d4a',
    black: '#3D3B3C',
  },
  fontFamily: {
    primary: 'Montserrat',
    secondary: 'Open Sans',
  },
  paddingSize: '20px',
  defaultRadius: '4px',
};
